import $ from 'jquery';
import NavMobile from './nav-mobile';
import { tree, current, treeSH, currentSH, currentURL } from './parse-nav';
import HomeLink from './home-link';

function launch(el) {
    let element = $(el);

    const containerTop = element.find('.nav-mobile__content');
    const containerTri = element.find('.nav-mobile__tertiary__children');

    if (currentURL.includes('bayes.city.ac.uk/staff-hub')) {  // Staffhub(SH) uses different tree due to being type2 asset in Matrix
        let nav = new NavMobile(element, treeSH, currentSH, containerTri);
        nav.launch();
        HomeLink(containerTop);
    } else if (currentURL.includes('bayes.city.ac.uk/alumni')) {  // Alumni uses different tree due to being type2 asset in Matrix
        let nav = new NavMobile(element, treeSH, currentSH, containerTri);
        nav.launch();
        HomeLink(containerTop);
    }
    else {
        let nav = new NavMobile(element, tree, current, containerTop);
        nav.launch();
    }
}

export default {
    className: 'nav-mobile',
    launch,
};
