import $ from 'jquery';
import findHeadings from './common/prepare-sections';
import prepareContent from './common/prepare-content';
import prepareLinks from './common/prepare-links';

function launch(el, i) {

    let headingLevel;

    $.each(el.attributes, function() {
        this.name === "data-level" ? headingLevel = this.value : null;
    });

    let widget = $(el);
    let contentWrapper = prepareContent(className, widget);
    let headings = findHeadings(className, widget, contentWrapper, headingLevel);

    prepareLinks(widget, headings, null);
}

const className = 'accordion';

export default {
    launch,
    className,
};
