import $ from 'jquery';

const className = 'kis-widget';

function kisWidget(el) {
    let widget = $(el);
    let kisIframe = widget.find('#unistats-widget-frame');

    var update = function() {
        var src = kisIframe.attr('src'),
            currentHorizontal = src.indexOf('horizontal') >= 0,
            neededHorizontal = $(window).width() > 635;

        if (currentHorizontal !== neededHorizontal) {
            var nextSrc = neededHorizontal
                    ? src.replace(/vertical/, 'horizontal')
                    : src.replace(/horizontal/, 'vertical'),
                nextStyle = neededHorizontal
                    ? { width: '615px', height: '150px' }
                    : {
                          width: '190px',
                          height: '500px',
                      };

            kisIframe.attr('src', nextSrc);
            kisIframe.css(nextStyle);
            widget.toggleClass('kis-widget--vertical', !neededHorizontal);
        }
    };

    $(window).on('resize', () => {
        update();
    });
    update();
}

export default {
    launch: kisWidget,
    className,
};
