import actionOnScroll from '../action-on-scroll';
import browserSupport from '../../js-utils/browser-support';
import $ from 'jquery';
const className = 'bayes-home';

function launch (element){
  if (browserSupport.intersectionObserver) {
    let banner = $(element).has('.bayes-video-banner').length ? $(element).find('.bayes-video-banner') : $(element).find('.bayes-carousel');
    if (banner instanceof $) {
      banner = banner[0];
    }

      // what to do with observed elements
      let actionOnScrollAction = function(entries) {
        const leftSide = $(element).find('.bayes-left-sidebar');
        const rightSide = $(element).find('.bayes-right-sidebar');
          entries.forEach(entry => {
              if (entry.isIntersecting) {
                leftSide.removeClass('bayes-left-sidebar-scrolled');
                rightSide.removeClass('bayes-right-sidebar-scrolled');
              }else{
                leftSide.addClass('bayes-left-sidebar-scrolled');
                rightSide.addClass('bayes-right-sidebar-scrolled');
              }
          });
      };

      // set up the observer and observe our element
      let observer = new IntersectionObserver(actionOnScrollAction, {threshold: 0.5});
      observer.observe(banner);

    const easeElements = [].slice.call(element.querySelectorAll('.ease-into-view'));
  
    easeElements.forEach(e =>{
      actionOnScroll($(e), 'ease-into-view-completed', false, { threshold: 0.3 });
    });
  }
}

export default {
    launch,
    className,
};