import './polyfills.js';
import './plugins/plugins';

import patterns from './component-list';
import { parametersToObject } from './js-utils/util.js';

import './components/linkIcons/linkIcons.js';
import './components/video-preview/video-wrap.js';
import { devcorate } from './components/devcorate/devcorate.js';

function tryCatch(f) {
    try {
        f();
    } catch (e) {
        if (console && console.error) {
            console.error(e);
        }
    }
}

function launchPattern(pattern) {
    if (typeof pattern === 'function') {
        tryCatch(pattern);
    } else if (pattern.className) {
        let { className, launch } = pattern;
        const elements = document.querySelectorAll(`.${className}:not(.${className}-njs)`);
        elements.forEach((element) => {
            tryCatch(() => launch(element));
        });
    }
}

// DOM ready
document.addEventListener(
    'DOMContentLoaded', 
    () => {
    patterns.forEach(launchPattern);

    const parameters = parametersToObject(location.search);
    parameters['test'] &&
        devcorate(
            document.querySelector('body'),
            'test',
            parameters['test']
        );
    parameters['prod'] &&
        devcorate(
            document.querySelector('body'),
            'prod',
            parameters['prod']
        );
});
